
// import *as galleryItems from '../app.js'; // Ð·Ð°Ð±Ñ€Ð°Ñ‚ÑŒ Ð²ÑÑ‘
import {galleryItems} from './main-objects.js';
const refs = {
    galleryUl: document.querySelector('.js-gallery'),
    lightbox: document.querySelector('.lightbox'),
    lightboxImage: document.querySelector('.lightbox__image'),
    lightboxOverlay: document.querySelector('.lightbox__overlay'),
    closeModalBtn: document.querySelector('[data-action="close-lightbox"]'),
  };
  
  refs.galleryUl.addEventListener('click', onOpenClickGallery);
  refs.closeModalBtn.addEventListener('click', CloseModalBtn);
  refs.lightboxOverlay.addEventListener('click', onBackdropClick);
  
  //ÑÐ¾Ð·Ð´Ð°Ð» Ñ€Ð°Ð·Ð¼ÐµÑ‚ÐºÑƒ Ð³Ð°Ð»ÐµÑ€ÐµÐ¸ Ñ‡ÐµÑ€ÐµÐ· Ð¼ÐµÑ‚Ð¾Ð´ map
  // const itemCardsGallery =  createElement ();
  function createElement () {
  return galleryItems
      .map(({ original, preview, description }, index) => {
        return `
      <li class = gallery__item> 
        <a href="${original}" class = gallery__link>
          <img 
            class = gallery__image 
            src="${preview}" 
            alt= "${description}" 
            data-source='${original}'
            data-index='${index}'> 
        </a>
      </li>
  `;
      })
      .join('');
    };
  refs.galleryUl.insertAdjacentHTML('beforeend', createElement());
  console.log(refs.galleryUl);
  
  //Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð½Ð° ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ðµ Ð´Ð»Ñ Ð¿Ñ€Ð¾ÑÐ¼Ð¾Ñ‚Ñ€Ð° Ð¸Ð·Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ñ Ð² Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ³Ð½Ð¾Ð¼ Ð¾ÐºÐ½Ðµ
  function onOpenClickGallery(event) {
    window.addEventListener('keydown', onEscKeydown);
    event.preventDefault();
    if (event.target.nodeName === 'IMG') {
      refs.lightbox.classList.add('is-open');
      refs.lightboxImage.src = event.target.getAttribute('data-source');
      refs.lightboxImage.alt = event.target.alt;
      refs.lightboxImage.dataset.index = event.target.dataset.index;
    }
    console.log(event.target.nodeName);
    // const isImagesLightboxEl = event.target.classList.contains('lightbox__image');
    // if(!isImagesLightboxEl) {
    //   return;
    // }
    //  if (event.target.nodeName === 'IMG') {
    //  return;
    //  }
  }
  
  //Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°Ð½Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ñ€Ð¸ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ð¸ Ð½Ð° ÐºÐ½Ð¾Ð¿ÐºÑƒ
  function CloseModalBtn(event) {
    window.removeEventListener('keydown', onEscKeydown);
    refs.lightbox.classList.remove('is-open');
    refs.lightboxImage.src = '';
    refs.lightboxImage.alt = '';
  }
  
  //Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°Ð½Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ñ€Ð¸ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ð¸ Ð½Ð° Ð±ÐµÐºÐ´Ñ€Ð¾Ð¿-Ð¾Ð²ÐµÑ€Ð»ÐµÐ¹
  function onBackdropClick(event) {
    if (event.currentTarget === event.target) {
      CloseModalBtn();
    }
  }
  
  //Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð´Ð»Ñ Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°Ð½Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ¾Ð³Ð¾ Ð¾ÐºÐ½Ð° Ð¿Ñ€Ð¸ Ð½Ð°Ð¶Ð°Ñ‚Ð¸Ð¸ Ð½Ð° esc
  function onEscKeydown(event) {
    const ESC_KEY_CODE = "Escape";
    if (event.code === ESC_KEY_CODE) {
      CloseModalBtn();
    }
    console.log(event);
  }
    
  //ÑÐºÑ€Ð¸Ð¿Ñ‚ Ð´Ð»Ñ Ð¿ÐµÑ€ÐµÐ»Ð¸ÑÑ‚Ñ‹Ð²Ð°Ð½Ð¸Ñ ÐºÐ°Ñ€Ñ‚Ð¸Ð½Ð¾Ðº ÐºÐ»Ð°Ð²Ð¸ÑˆÐ°Ð¼Ð¸ Ð²Ð¿Ñ€Ð°Ð²Ð¾ Ð¸ Ð²Ð»ÐµÐ²Ð¾ 
  window.addEventListener("keydown", (event) => {
    if (event.code === "ArrowLeft") {
      onArrowLeft();
    }
    if (event.code === "ArrowRight") {
      onArrowRight();
    }
  });
  
  function onArrowLeft() {
    let index = +refs.lightboxImage.dataset.index;
    if (index === 0) {
      step(galleryItems.length - 1);
      return;
    }
    step(index, -1);
  }
  function onArrowRight() {
    let index = +refs.lightboxImage.dataset.index;
    if (index === galleryItems.length - 1) {
      step(0);
      return;
    }
    step(index, 1);
  }
  
  function step(index, step = 0) {
    refs.lightboxImage.dataset.index = `${index + step}`;
    refs.lightboxImage.src = galleryItems[index + step].original;
  }
  